
import headTop from "@/components/header";
import footerNav from "@/components/footer";
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Badge from 'vux/src/components/badge/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'

import { mapGetters, mapMutations } from "vuex";
import Api from "@/js/common";
import { setTimeout } from "timers";

export default {
data() {
return {
index: 0,
getBarWidth: function(index) {
return 1 * 10 + "rem";
}
};
},
methods: {
onItemClick(index) {
this.index = index;
Api.setVal("tabIndex", index);
if (index == 0) {
this.$router.replace({ name: "getQustionList" });
} else {
this.$router.replace({ name: "creatQuestionList" });
}
}
},
computed: {
...mapGetters(["isLogin"]),
...mapGetters(["device"])
},
created() {






















},
beforeRouteEnter(to, from, next) {
next(vm => {
if (from.name !== "creatQuestionList" || from.name !== "getQustionList") {

vm.index = 0;
}
if (to.name === "creatQuestionList") {
vm.index = 1;
}
if (to.name === "getQustionList") {
vm.index = 0;
}
});
},
mounted() {
appcan.setNavigationBarTitle({
title: "调查问卷"
});
},
components: {
headTop,
Tab,
TabItem,
Badge,
footerNav,
ViewBox
}
};



